@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body {
  margin: 0;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.main-header {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  color: #fff;
  font-size: 2rem;
  background: #333333;
}

.main-heading {
  display: flex;
  justify-content: center;
  margin: 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.main {
  flex: 1 0;
  padding-top: 1rem;
  background: #c9c9c9;
}

.form-bg {
  background: linear-gradient(
    to top,
    rgb(41, 128, 185),
    rgb(109, 213, 250),
    rgb(255, 255, 255)
  );
}

.form-section + .form-section {
  margin-top: 1rem;
}

.form-input {
  display: block;
  width: 100%;
  padding: 0.3rem;
  font-size: 1rem;
  resize: none;
  border-radius: 0.2rem;
}

.label {
  display: block;
}

.form-control {
  margin-top: 1rem;
}

.btn-add {
  --background-color: #c6c6fe;
  --hover-background-color: #aaaafe;
}

.btn {
  padding: 0.5rem;
  background: var(--background-color);
  font: inherit;
}

.btn:hover,
.btn:active {
  background: var(--hover-background-color);
}

.button-group {
  margin-top: 1rem;
}

.btn-delete {
  --background-color: #febbb0;
  --hover-background-color: #fd9782;
}

.form-partial {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.form-partial + .form-partial {
  margin-top: 1rem;
}

.btn-done {
  --background-color: #92fd79;
  --hover-background-color: #60e831;
}

.panel {
  padding: 1rem;
  border-radius: 0.2rem;
}

.btn-edit {
  --background-color: #a5d0f4;
  --hover-background-color: #6cbaef;
}

.result-bg {
  background: linear-gradient(
    to top,
    rgb(127, 127, 213),
    rgb(134, 168, 231),
    rgb(145, 234, 228)
  );
}

.result-section + .result-section {
  margin-top: 1rem;
}

.result-section--main {
  display: flex;
}

.result-info {
  margin-left: auto;
}

.result-mainheading {
  font-size: 4rem;
}

.result-subheading {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.result-label {
  margin: 1rem 0 0 0;
  font-weight: bold;
}

.result-text {
  margin: 0.5rem 0 0 0;
}

.result-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

